.experience-container {
  margin: 13rem 0 0 0;
  padding: 0 17rem;
  position: relative;
}

.experience-container h5 {
  padding: 0 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 3rem;
}

.arrow-left,
.arrow-right {
  width: 2.2rem;
  height: 2.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6751b9;
  border-radius: 0.65rem;
  border: 1.5px solid #6751b9;
  background: #130f2a;
  position: absolute;
  top: 55%;
  z-index: 2;
  cursor: pointer;
}

.arrow-left span,
.arrow-right span {
  font-size: 2rem;
  font-weight: 500;
}

.arrow-left {
  left: 8rem;
}

.arrow-right {
  right: 8rem;
}

@media (max-width: 1740px) {
  .experience-container {
    padding: 0 2rem;
  }

  .arrow-right {
    right: 2rem;
  }

  .arrow-left {
    left: 2rem;
  }
}

@media (max-width: 768px) {
  .experience-container {
    padding: 0 2rem;
  }
  .experience-container h5 {
    font-size: 1.3rem;
    margin-bottom: 2rem;
  }
  .arrow-right {
    right: 1rem;
  }

  .arrow-left {
    left: 1rem;
  }
}
