.contact-form-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-bottom: 2rem;
}

form {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
}

.name-container {
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  width: 100%;
}

.email-input {
  height: 50px;
}

form input,
form textarea {
  flex: 1;
  width: 100%;
  font-size: 1.1rem;
  color: white;
  background: #130f2a;
  border-radius: 0.6rem;
  border: 1.5px solid #6751b9;
  padding: 1rem;
  box-sizing: border-box;
}

form button {
  font-size: 1rem;
  font-weight: 500;
  color: white;
  border: 1.5px solid #6751b9;
  border-radius: 0.6rem;
  background: linear-gradient(90deg, #a892fe 0%, #8064e8 100%);
  padding: 0.7rem;
  transition: all 0.3s ease;
  cursor: pointer;
  width: 100%;
}

form button:hover {
  color: #a892fe;
  background: transparent;
}

@media (max-width: 450px) {
  .name-container {
    flex-direction: column;
    align-items: flex-start;
  }
  .form input,
  form textarea {
    padding: 0.7rem;
    border-radius: 0.3rem;
  }
  form button {
    font-size: 0.9rem;
    padding: 0.7rem;
    border-radius: 0.3rem;
  }
}
